.page-container {
  height: 100vh;
  padding-left: 10%;
  padding-right: 10%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  height: max-content;
  flex-flow: row wrap;
}
.page-section {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-top: 100px;
  margin-bottom: 100px;
  padding: 50px;
  background-color: black;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.507);
}

h1 {
  margin-top: 25px;
  margin-bottom: 0px;
  text-align: left;
}

p {
  margin-top: 15px;
  margin-bottom: 5px;
}

.page-section button {
  width: 100px;
  height: 30px;
  border: none;
  background-color: #1A1A1A;
  color: white;
  font-size: 16px;
}
.page-section button:hover {
  cursor: pointer;
  
}
.page-section button:active {
  cursor: pointer;
  transform: scale(1);
  box-shadow: 0px 1px 2.5px rgb(180, 180, 180);
}
.page-content img {
  margin-top: 50px;
  padding: 20px;
  padding-left: 50px;
  height: 400px;
  width: 400px;
}
.page-container button {
  width: 250px;
  height: 60px;
  border: none;
  font-weight: bold;
  margin-top: 15px;

  cursor: pointer;
  align-items: left;
  font-size: 16px;
}

.page-container button:hover {
  background-color: white;
  color: black;
}
