.partners-container {
  margin: 0px;
  background-image: url(../images/home-bg-small.jpg);
  background-position: center;
  background-size: cover;
  padding-top: 100px;
  padding-bottom: 200px;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  height: max-content;
  flex-flow: row wrap;
}
.partners-content {
  display: flex;
  flex-wrap: wrap;
  align-content: space-evenly;
  grid-template-columns: auto auto auto;
  gap: 15px;
  width: 100%;
  justify-content: center;
}
.partners-logo {
}
.partners-logo img {
  height: 300px;
}

.partners-title {
  overflow: hidden;
  position: relative;
  display: grid;
  cursor: default;
}

.jeets img {
  width: 300px;
}

.warning-card {
  border-style: outset;

  background-color: lightgrey;
  margin-left: 10%;
  margin-right: 10%;
  width: 500px;
}
.warning-card-header {
  background-image: linear-gradient(
    to right,
    rgb(0, 68, 255),
    rgb(0, 153, 255)
  );
  height: 20px;
  padding-left: 15px;
  padding-right: 15px;
}

.warning-card-body button {
  display: block;
  margin: auto;
  float: right;
  width: 100px;
  border-style: outset;
  background-color: lightgrey;
}

.warning-card-body button:active {
  display: block;
  margin: auto;
  float: right;
  width: 100px;
  border-style: inset;
  background-color: lightgrey;
}

.partners-container h1 {
  margin-top: 15px;
  margin-bottom: 5px;
  margin-left: 15px;
  font-weight: bold;
  text-align: left;
  font-size: 18px;
  color: black;
}

.partners-container p {
  margin-top: 5px;
  margin-bottom: 5px;
  margin-left: 15px;
  margin-right: 15px;
  font-weight: lighter;
  text-align: left;
  font-size: 12px;
  color: black;
}

@media only screen and (max-width: 700px) {
  .partners-content {
    flex-direction: column;
  }
  .warning-card {
    background-color: lightgrey;
    width: 80%;
    margin-left: 10%;
    margin-right: 10%;
  }

  .jeets img {
    width: 300px;
  }
}
