.footer {
  width: 100%;
  min-height: 100px;
  height: 100%;
  margin: 0;
  background: #000000;
  text-align: center;
  margin-bottom: 0px;
  padding-top: 25px;
}
.footer-text {
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
}

.footer-text p {
  font-size: 12px;
}

.footer-links p {
  font-size: 12px;
  margin-bottom: 0px;
  margin-top: 0px;
  text-align: center;
  flex-direction: row;
}

.footer-links {
    display: flex;
    flex-wrap: wrap;
    align-content: space-evenly;
    grid-template-columns: auto auto auto;
    gap: 15px;
    justify-content: center;
}
.footer-links a {
    margin-bottom: 0px;
    margin-top: 0px;
  }

.social-links img {
  height: 20px;
  margin: 20px;
  margin-bottom: 0px;
  cursor: pointer;
}
