.web3-container {
  margin: 0px auto;
  background-image: linear-gradient(rgb(8, 8, 8), rgb(0, 0, 0));
  background-position: center;
  background-size: cover;

  padding-top: 100px;
  padding-bottom: 100px;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-around;
  height: max-content;
  flex-flow: row wrap;
}
.web3-content {
  display: flex;
  flex-wrap: wrap;
  align-content: space-evenly;
  grid-template-columns: auto auto auto;
 
  width: 100%;
  justify-content: center;
}
.web3-card {
  overflow: hidden;
  position: relative;
  display: grid;
  width: 50%;
  cursor: default;
  height: 400px;
  border-style: solid;
  border-width: thin;
  background-color: #0D0D0D;
}
.red-bar {
  height: 5px;
  background-color: rgb(255, 55, 55);
}
.blue-bar {
  height: 5px;
  background-color: rgb(0, 238, 255);
}
.web3-card:hover {
  transition: .03s;
  transform: scale(1.01)
}
.web3-card h1 {
  color: rgb(255, 255, 255);
  font-size: 28px;
  margin-top: 15px;
}

.web3-card h2 {
  margin-bottom: 0px;
  color: white;
  text-align: left;
  font-weight: lighter;
}
.web3-card button p {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 15px;
  color: white;

}
.web3-card button {
  border: none;
  width: 100%;
  height: 50px;
  margin-top: 45%;
  color: white;
  background: #1A1A1A;
  cursor: pointer;
}
.web3-card button:hover {
  color:black;
}

.card-info {
  height: 150px;
  padding-left: 15px;
  padding-right: 15px;
}
.card-link button {
  margin-left: -15px;
  margin-bottom: -10px;
}

@media only screen and (max-width: 1000px) {
  .web3-content {
    flex-direction: column;
  }
  
}
