.header-container {
  height: 70vh;
  display: flex;
  padding: 115px;

  background-image: url(../images/home-bg.jpg);
  background-size: cover;

  background-position: center;
  background-repeat: no-repeat;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  flex-flow: row wrap;
}
.header-container .header-content {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 450px;
}


.header-content h1 {
  margin-top: 0px;
  margin-bottom: 0px;
  text-align: left;
  font-size: 64px;
  color: white;
}
.header-content p {
  margin-top: 15px;
  font-size: 20px;
  color: white;
  font-weight: bold;
}
.header-container button {
  width: 200px;
  height: 50px;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  margin-left: -15px;
  margin-top: 15px;
  align-items: left;
  background-image: none;
  background-color: rgb(255, 255, 255);
  color: rgb(68, 171, 255);
  font-size: 16px;
}
.header-content a {
  color: white;
}

.header-content button:hover {
  cursor: pointer;
  background-color: white;
  color: rgb(68, 171, 255);
}
.header-content button:active {
  cursor: pointer;
  transform: scale(1);
}
.header-content img {
  margin-top: 40px;
  padding-left: 100px;
  height: 300px;
  width: 300px;
  
}

.network-img img {
  margin-top: 35px;

  height: 350px;
  width: 350px;
}
.art-details p {
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
  margin-left: 100px;
  margin-top: 15px;
  font-style: italic;
  font-weight: thin;
  cursor: default;
}

@media only screen and (max-width: 800px) {
  .header-container {
    height: 100vh;
    margin-bottom: 25px;
  }
  .header-content {
    width: 600px;
  }

  .header-content img {
    height: 300px;
    width: 300px;
  }
  .network-img img {
    margin-top: -150px;
    margin-bottom: -200px;
    height: 300px;
    width: 300px;
  }
}
