.call-container {
  height: 60vh;
  display: flex;
  padding: 15px;
  padding-top: 5%;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  flex-flow: row wrap;
  background-color: #141416;
}
.call-container .call-content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 450px;
}
.call-content h1 {
  margin-top: 80px;
  margin-bottom: 0px;
  text-align: left;
  font-size: 32px;
  color: white;
}
.call-content p {
  margin-top: 15px;
  font-size: 20px;
  color: white;
}
.call-container button {
  width: 250px;
  height: 60px;
  border: none;
  font-weight: bold;
  margin-left: -15px;
  margin-top: 15px;
  align-items: left;
  background-image: none;
  background-color: white;
  border-radius: 5px;
  color: black;
  font-size: 16px;
}
.call-content a {
  color: white;
}

.call-content button:hover {
  cursor: pointer;
  box-shadow: 0px 2.3px 5.3px rgb(0, 0, 0);
  background-color: white;
}
.call-content button:active {
  cursor: pointer;
  transform: scale(1);
  box-shadow: 0px 1px 2.5px rgb(180, 180, 180);
}
.call-content img {
  margin-top: 40px;

  height: 300px;
  width: 300px;
}

.network-img img {
  margin-top: 35px;

  height: 350px;
  width: 350px;
}
.art-details p {
  color: rgba(255, 255, 255, 0.5);
  font-size: 12px;
  margin-left: 100px;
  margin-top: 15px;
  font-style: italic;
  font-weight: thin;
  cursor: default;
}

@media only screen and (max-width: 1000px) {
  .call-container {
    height: 100%;
    padding-bottom: 100px;

  }
  .call-content {
    width: 600px;
  }
  .call-content h1 {
    margin-top: 50px;
  }

  .call-content img {
    height: 300px;
    width: 300px;
  }
  .network-img img {
    margin-top: -150px;
    margin-bottom: -200px;
    height: 350px;
    width: 350px;
  }
}
