/* This is the styling for the nav container */
.navbar {
  width: 100vw;
  height: 0px;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0);
}
/* This is the styling for the nav content */

.navbar-content {
  padding-top: 15px;
  width: 100%;
  height: 70px;
  align-items: center;
  display: flex;
}

/* This is the styling for the left nav container */

.left-nav {
  color: #fff;
  flex: 30%;
  display: flex;
  align-items: center;
  padding-left: 5%;
}
.left-nav h1 {
  font-size: 18px;
  margin-top: 0px;
  margin-left: 20px;
}
.left-nav img {
  width: 200px;
  padding-top: 15px;
  padding-bottom: 15px;
}
/* This is the styling for the right nav container */

.right-nav {
  flex: 70%;
  display: flex;
  justify-content: flex-end;
  padding-right: 20px;
  padding-top: 15px;
  padding-bottom: 15px;
}

a {
  color: rgb(221, 221, 221);
  font-size: large;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  margin: 15px;
}
a:hover {
  color: #fff;
  font-size: large;
  text-decoration: none;
  font-family: Arial, Helvetica, sans-serif;
  margin: 15px;
}

a:active {
  color: rgb(180, 180, 180);
}
.navbar-content button {
  width: 150px;
  height: 50px;
  border: none;
  font-weight: bold;
  margin-left: -15px;
  margin-top: 15px;
  align-items: left;
  background-image: none;
  background-color: white;

  font-size: 16px;
}

.navbar-content button:hover {
  cursor: pointer;
  transform: scale(1.05);
}
.navbar-content button:active {
  cursor: pointer;
  transform: scale(1);
}
.right-nav img {
  height: 20px;
  margin: 5px;
  margin-bottom: 0px;
  cursor: pointer;
}
