.box {
  height: 300px;
  padding: 0px;
  margin-top: -25px;
}
.row {
  display: flex;
  flex-wrap: wrap;
}
.container {
  flex: 1 1 200px;
  height: 50px;
}
.buy {
  background-color: #FFCB67;
  height: 300px;
  color: white;
}

.buy a {
  color: white;
}
.join {
  background-color: #214E81;
  height: 300px;
}

.container {
  width: 200px;
  height: 200px;
  background-color: #0a0a23;
  text-align: center;
}

.container img {
  width: 200px;
  padding-top: 50px;
  margin: auto;
  display: block;
}

@media only screen and (max-width: 600px) {
  .row {
    flex-direction: column;
  }
  .box {
    height: auto;
    padding: 0px;
  }
}
